// Global styles 
/* Global styles */


html {
    box-sizing: border-box;
    min-width: 320px;
     
}

*,
*::after,
*::before {
    box-sizing: inherit;
}

body {
    // font-size: 16px;
    // font-style: 400;
    // overflow-x: hidden;
    overflow-x: hidden;
    position: relative;
    font-family: 'Geometria', sans-serif;
    font-weight: normal;
    color: #000;
    background-color: $background;
     
}

.container {
    max-width: 1170px;
    padding: 0 15px;
    margin: 0 auto;
}

ul,
li {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    display: inline-block;
    color: #000;
    text-transform: none;

}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-size: inherit;
}
 
